// window.addEventListener('DOMContentLoaded', () => { 
  const cardsInner = document.querySelector('.products__inner')
  const filterPrice = document.querySelector('#filter-price')
  const filterAge = document.querySelector('#filter-age')

  filterPrice.addEventListener('click', sortCards) 
  filterAge.addEventListener('click', sortCards)

  function sortCards (event){  
    setTimeout(()=> closeFilter(), 100)    
    closeFilter()
    if(this.hasAttribute('open') && !event.target.classList.contains('products-filter-details__name')){      
      if(this.id == "filter-price"){
        cards.sort((a, b) => {
          a = a.price.replace(/\s/, '');            
          b = b.price.replace(/\s/, ''); 
          switch (event.target.textContent) {
            case 'По возрастанию':                 
              return b - a;
            case 'По убыванию':
              return a - b;            
          }
        })
      } else if(this.id == "filter-age"){
        cards.sort((a, b) => {
          a = parseInt(a.age);            
          b = parseInt(b.age); 
          switch (event.target.textContent) {
            case 'По возрастанию':           
              return b - a;
            case 'По убыванию':
              return a - b;            
          }
        }); 
      }
      addProducts(cards);
      removeActiveFilter();
      event.target.parentNode.classList.add('products-filter-details__item--active')
      setTimeout(()=> this.removeAttribute('open'), 100)
      checkStatusDisabled()
      checkHeart();
    }
  }

  function removeActiveFilter() {
    let filterItems = document.querySelectorAll('.products-filter-details__item')
    filterItems.forEach(el=> {     
        if(el.classList.contains('products-filter-details__item--active')){    
          filterItems.forEach(item => {            
            item.classList.remove('products-filter-details__item--active')   
          })
        }             
    })
  }
  
  function closeFilter() {
    const filters = document.querySelectorAll('.products-filter-details')
    filters.forEach(filter => {
      filter.addEventListener('click', ()=> {
        filters.forEach(el =>{
          if(el !== filter) el.removeAttribute('open')     
        })
      })     
      if(filter.hasAttribute('open')){    
        const removeOpenedFilter = (event) =>{
          if( !event.target.closest('.products-filter-details') ){
            setTimeout(()=> filter.removeAttribute('open'), 150)  
          }
          window.removeEventListener('click', removeOpenedFilter)
        }
        window.addEventListener('click', removeOpenedFilter)
      }
         
      // filter.hasAttribute('open')
    
    
    })
   
  }


  checkHeart();
  function checkHeart() {
    if(document.querySelector('.card-top__heart')){
      const hearts    = document.querySelectorAll('.card-top__heart');    
      let createPopup = document.createElement('span')
      let time;
      for(let i = 0; i<hearts.length; i++){
        let heart = hearts[i];
        heart.addEventListener('click', function()  {
          if(heart.classList.contains('--active')){
            heart.classList.remove('--active');  
            clearTimeout(time) 
            createPopup.className = "popup popup_remove"
            createPopup.innerHTML="Удалено из избранное"
            document.body.append(createPopup);
            time = setTimeout(()=> document.querySelector('.popup').remove(), 2000);
          } else{
            heart.classList.add('--active');
            clearTimeout(time)
            createPopup.className = "popup popup_add"
            createPopup.innerHTML="Добавлено в избранное"
            document.body.append(createPopup);
            time = setTimeout(()=> document.querySelector('.popup').remove(), 2000);
          }       
        })
      }    
    }
  }

  checkStatusDisabled()
