  showBurger();
  function showBurger() {
    document.querySelector('.header-top__burger').addEventListener('click', function() {  
      this.classList.toggle('--active')  
      const menu = document.querySelector('.menu');
      document.body.classList.toggle('lock')
      menu.classList.toggle('--active')
    });
  }


  
  document.querySelectorAll('.--link').forEach(link => {
    link.addEventListener('click', function(e) {
      e.preventDefault();
      scrollToId(this.hash); 
    })
  });  
  function scrollToId(id) {
    let section = document.querySelector(id);
    let pos = section.offsetTop - 55

    if("scrollBehavior" in document.body.style){
      window.scrollTo( {
        top: section.offsetTop - 55,
        behavior: "smooth"
      })
    } else {
      window.scrollTo(0, pos);
    }
  }

  const form = document.querySelector('.offer__form');
  form.addEventListener('submit', formSend);

  async function formSend (e) {
    e.preventDefault();
    let error = formValidate(form);

    let formData = new FormData(form);

    if(error === 0) {
      document.body.classList.add('--sending');
      let response = await fetch('../docs/mail.php', {
       method: 'POST',
       body: formData,
      });
      if(response.ok){
        let result = await response.json();
        document.querySelector('.offer__right').innerHTML = `<span class="offer__result">${result}</span>`
        document.body.classList.remove('--sending')
      } else{
        form.insertAdjacentHTML('afterbegin', `<span class="offer__result">${result}</span>`)
        document.body.classList.remove('--sending')
      }
    } else{
      if(!document.querySelector('.offer__status-error')){
        form.insertAdjacentHTML('beforebegin', '<span class="offer__status-error">Заполните обязательные поля</span>')
      }
    }
  }


  function formValidate (form) {
    let error = 0;
    let input = document.querySelector('.form__email')
    formRemoveError(input);
      if(input.classList.contains('--email')) {
        if(emailTest(input)){
          formAddError(input);
          error++;
        }
      } else{
          if(input.value === '') {
            formAddError(input);
            error++;
          }
      }
    
    return error;
  }
  function formAddError(input) {
    input.classList.add('--error');
  }
  function formRemoveError(input) {
    input.classList.remove('--error');
  }
  // Функция теста email
  function emailTest(input) {
    return !/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,8})+$/.test(input.value);
  }

  window.addEventListener('scroll', () => {
    if(window.scrollY > 250){
      document.querySelector('.arrow-up').classList.add('--visible')
    } else if(window.scrollY < 250){
      document.querySelector('.arrow-up').classList.remove('--visible')
    }
    if(window.innerWidth < 767 && window.scrollY > 160){
      document.querySelector('.header-top').classList.add('--fixed')
    } else {
      document.querySelector('.header-top').classList.remove('--fixed')
    }
    
  })
  
